








import { Component, Prop, Vue } from 'vue-property-decorator';
import { Company } from '@/datatypes';

@Component
export default class CompanyList extends Vue {
  @Prop({default: ''})
  private filter?: string;
  @Prop({default: false})
  private computer?: boolean;
  @Prop({default: false})
  private software?: boolean;
  @Prop({default: false})
  private electrical?: boolean;

  get companies(): Company[] {
    return this.$store.state.companies;
  }

  get filteredCompanies(): Company[] {
    return this.companies.filter((company) => {
      if (typeof this.filter === 'string' &&
        this.filter.length !== 0 &&
        company.name.toLowerCase().indexOf(this.filter.toLowerCase()) === -1) {
          return false;
      }
      if (this.computer && !company.divisions.includes('D')) {
        return false;
      }
      if (this.software && !company.divisions.includes('IT')) {
        return false;
      }
      if (this.electrical && !company.divisions.includes('E')) {
        return false;
      }
      return true;
    });
  }
}
