















import { Component, Prop, Vue } from 'vue-property-decorator';

interface MenuStyleInterface {
  display: string;
}

@Component
export default class NavBar extends Vue {
  private searchQuery: string = 'Hej';

  private menubarStyle: MenuStyleInterface = {
    display: 'none',
  };

  private toggledMenubar: boolean = false;

  @Prop() private titleVisible!: boolean;
  @Prop() private searchQueryVisible!: boolean;

  /*private menuStyle(): MenuStyleInterface {
    return {
      display: this.toggledMenubar ? 'block' : 'hidden',
    };
  }*/

  private toggleMenu(): void {
    this.toggledMenubar = !this.toggledMenubar;
    this.menubarStyle.display = this.toggledMenubar ? 'block' : 'none';
  }
}
