import Vue from 'vue';
import Vuex from 'vuex';

import {Company, /*Booth,*/ Coordinates, DataResponse} from './datatypes';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    companies: Array<Company>(),
    // booths: Array<Booth>(),
  },
  mutations: {
    setCompanies(state, companies: Company[]) {
      state.companies = companies;
    },
    /*setBooths(state, booths: Booth[]) {
      state.booths = booths;
    },*/
  },
  actions: {
    async fetchAndInsertData(context) {
      const fetchResponse = await fetch('/api/data.json', {
        method: 'GET',
        cache: 'default',
      });

      const company = Array<Company>();

      const jsonData = (await fetchResponse.json()) as DataResponse;

      jsonData.companies.forEach((jcompany) => {
        // Get the booth or set it to error
        /*const booth = jsonData.booths.find((jbooth) => jbooth.name === jcompany.booth) || ({
          name: 'error',
          coordinates: {
            x: 0,
            y: 0,
          },
        } as Booth);*/
        const companyData: Company = {
          id: jcompany.id,
          name: jcompany.name,
          imageUrl: jcompany.imageUrl,
          divisions: jcompany.divisions,
          offerings: jcompany.offerings,
          description: jcompany.description,
          fact: jcompany.fact,
          startYear: jcompany.startYear,
          employeesWorldwide: jcompany.employeesWorldwide,
          employeesSweden: jcompany.employeesSweden,
          website: jcompany.website,
          contactEmail: jcompany.contactEmail,
          locations: jcompany.locations,
          booth: jcompany.booth,
        };
        company.push(companyData);
      });

      // context.commit('setBooths', jsonData.booths);
      context.commit('setCompanies', company);
    },
  },
});
